body {
  font-family: $sansSerif;
  font-size: 16px;
  line-height: 1.5;
  color: $black;
  -webkit-font-smoothing: antialiased;
}
input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
a {
  color: $blue;
  text-decoration: none;
}
a:hover,
a:focus {
  text-decoration: underline;
}
a:focus {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}
figure {
  margin: 0;
}
img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}
hr {
  margin: 1em 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.5;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
h2 {
  font-size: 1.5em;
  margin: 0.83em 0;
}
h3 {
  font-size: 1.17em;
  margin: 1em 0;
}
h4 {
  font-size: 1em;
  margin: 1.33em 0;
}
h5 {
  font-size: 0.83em;
  margin: 1.67em 0;
}
h6 {
  font-size: 0.67em;
  margin: 2.33em 0;
}
p {
  margin: 0 0 1em;
}
small {
  font-size: 85%;
}
mark {
  background-color: $yellow;
  padding: 0.2em;
}
ul,
ol {
  margin-top: 0;
  margin-bottom: 1em;
  padding-left: 2.5em;
}
ul ul,
ol ul,
ul ol,
ol ol {
  margin-bottom: 0;
}
dl {
  margin-top: 0;
  margin-bottom: 1em;
}
dt {
  font-weight: bold;
}
dd {
  margin-left: 0;
}
blockquote {
  padding: 0.625em 1.875em;
  margin: 0 0 1em;
  font-style: italic;
}
blockquote p:last-child,
blockquote ul:last-child,
blockquote ol:last-child {
  margin-bottom: 0;
}
